import { Component, h, Element, Prop, Listen, Build } from '@stencil/core';
import noUiSlider from 'nouislider'

@Component({
    tag: 'ks-filter-slider',
    styleUrl: 'filter-slider.css'
})
export class FilterSlider {

    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() values: string;
    @Prop() snap: boolean = false;
    @Prop() step: number = 0;
    @Prop() steps: string = "";

    @Prop({mutable: true}) from: number;
    @Prop({mutable: true}) to: number;

    valueArray: number[];

    handleActive: boolean = false;

    @Listen('swipeLeft')
    swipeLeftHandler(event: Event) {
        event.stopPropagation();
    }
    @Listen('click')
    clickHandler(event: Event) {
        event.stopPropagation();
    }

    componentWillLoad() {
        this.valueArray = this.values.split(", ").map(
            value => parseFloat(value)
        );
    }

    componentDidLoad(){
        if(!Build.isBrowser) return;

        const slider = this.root.firstElementChild;
        const steps = this.steps.split(", ").map(
            value => parseInt(value)
        );

        const range = this.range(this.valueArray, steps);

        const step = steps[0] ? steps[0] : this.step;
        const asint = this.step || this.steps;

        noUiSlider.create((slider as HTMLElement), {
            start: [range["min"], range["max"]],
            snap: this.snap,
            step: step,
            tooltips: true,
            connect: true,
            range: range,

            format: {
                to: function (value) {
                    return asint ? Math.round(value) : value;
                },
                from: function (value) {
                    return asint ? Math.round(value) : value;
                }
            }
        });

        const sliderInstance = slider as noUiSlider.Instance;
        
        sliderInstance.noUiSlider.on("set", ()=>{
            const range = sliderInstance.noUiSlider.get();
            this.from = parseFloat(range[0]);
            this.to = parseFloat(range[1]);
        });

        sliderInstance.noUiSlider.set([this.from, this.to]);
    }

    private range(values: number[], steps?: number[]) {
        return values.reduce((o, value, index) => {
            const step = steps[index] ? [steps[index]] : []

            if(index == 0) o["min"] = [value];
            else if(index == values.length-1) o["max"] = [value];
            else o[(index / (values.length-1))*100 + "%"] = [value, ...step];
            return o;
        }, {})
    }

    render() {
        const disabled = this.from === undefined || this.to === undefined || (
            this.from == this.valueArray[0] &&
            this.to == this.valueArray[this.valueArray.length-1]
        );

        const value = !disabled ? this.from + "," + this.to : "";

        return [
            <div></div>,
            <input type="hidden" name={this.name} value={value} disabled={disabled}></input>
        ];
    }
}

