import { Component, h, Prop } from '@stencil/core';
import { ColorList } from './colors'; 

@Component({
    tag: 'ks-filter-color',
    styleUrl: 'filter-color.css'
})
export class FilterColor {

    @Prop() name: string;
    @Prop() value: string;
    @Prop() color: string;
    @Prop({mutable: true}) active: boolean = false;

    hex: string;
    material: string;
    size: number = 20;

    componentWillLoad() {
        let found = ColorList.filter(c => c.name.includes(this.color));
        if(found.length > 0) {
            this.hex = found[0].color;
            this.material = found[0].material;
        }
    }

    metal = [
        <defs>
            <linearGradient id="Linear1" x1="0" y1="0" x2="1" y2="0" gradientUnits="userSpaceOnUse" gradientTransform="matrix(8,8,-8,8,13,13)">
                <stop offset="0" style={{stopColor: "white", stopOpacity: "0.4"}}/>
                <stop offset="1" style={{stopColor: "white", stopOpacity: "0"}}/>
            </linearGradient>
        </defs>,
        <g transform="matrix(1.25,0,0,1.25,-6,-36)">
            <g transform="matrix(1,0,0,1,0,24)">
                <path d="M21,21L5,21L21,5L21,21Z" style={{fill: "url(#Linear1)"}}/>
            </g>
        </g>
    ];

    wood = [
        <g transform="matrix(0.6,0,0,0.6,-0,-0)">
            <circle cx="28.634" cy="28.483" r="6.483" style={{fill: "none", stroke: "black", strokeOpacity: "0.20", strokeWidth: "3px"}}/>
            <g transform="matrix(2.30024,0,0,2.30024,-37.2311,-37.0348)">
                <circle cx="28.634" cy="28.483" r="6.483" style={{fill: "none", stroke: "black", strokeOpacity: "0.20", strokeWidth: "1.3px"}}/>
            </g>
            <g transform="matrix(3.70926,0,0,3.70926,-77.5769,-77.168)">
                <circle cx="28.634" cy="28.483" r="6.483" style={{fill: "none", stroke: "black", strokeOpacity: "0.20", strokeWidth: "0.94px"}}/>
            </g>
            <g transform="matrix(5.16472,0,0,5.16472,-119.253,-118.624)">
                <circle cx="28.634" cy="28.483" r="6.483" style={{fill: "none", stroke: "black", strokeOpacity: "0.20", strokeWidth: "0.77px"}}/>
            </g>
        </g>
    ];

    multicolor = [
        <rect width={this.size / 2} height={this.size / 2} style={{fill: "#fe4a49"}} transform="translate(0 0)"/>,
        <rect width={this.size / 2} height={this.size / 2} style={{fill: "#2ab7ca"}} transform={"translate(" + this.size / 2 + " 0)"}/>,
        <rect width={this.size / 2} height={this.size / 2} style={{fill: "#fed766"}} transform={"translate(0 " + this.size / 2 +")"}/>,
        <rect width={this.size / 2} height={this.size / 2} style={{fill: "#e6e6ea"}} transform={"translate(" + this.size / 2 + " "+ this.size / 2 +")"}/>
    ]

    change(e: Event) {
        this.active = (e.target as HTMLInputElement).checked;
    }

    render() {
        return <label>
            <svg width={this.size} height={this.size}>
                <rect width={this.size} height={this.size} style={{fill: this.hex}} />
                {this.material == "metal" ? this.metal : null}
                {this.material == "wood" ? this.wood : null}
                {this.material == "multicolor" ? this.multicolor : null}
            </svg>

            <input name={this.name} value={this.active ? this.value : ""} type="checkbox" checked={this.active} onChange={e=>this.change(e)}/>
            <span class="checkmark"/>
            {this.color}
        </label>;
    }
}