import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-filter',
    styleUrl: 'filter.css'
})
export class Filter {

    @Prop() name: string;
    @Prop({mutable: true, reflect: true}) active: boolean = false;

    render() {
        return[
            <div onClick={()=> this.active = !this.active}>
                {this.name}
                <ks-icon name={this.active ? "minus" : "plus"} size={0.9}/>
            </div>,
            <div class="filter-content">
                <slot/>
            </div>
        ];
    }
}